@import "~@/assets/scss/production.scss";@import "~@/assets/scss/variables.scss";@import "~@/assets/scss/functions.scss";@import "~@/assets/scss/mixin.scss";
@include b(promo) {
  overflow: hidden;
  background-color: #1e1e1e;

  @include e(content) {
    position: relative;
    width: $--content-width;
    margin: 40px auto;
    overflow: hidden;
    border-radius: 4px;
  }

  @include e(list) {
    position: relative;
    min-height: 60px * 8 + 65 * 2;
    padding: 44px 0;
    margin-left: 255px;
    background-color: #282828;
  }

  @include e(list-inner) {
    width: 760px;
    margin: 0 auto;
  }

  @include e(item) {
    & + & {
      margin-top: 40px;
    }
  }

  @include e(item-inner) {
    position: relative;
    height: 142px;
    margin-top: 10px;
    background-color: #1e1e1e;
    box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.2);
  }

  @include e(title) {
    font-size: 16px;
    color: #d1d1d1;
  }

  @include e(img-wrapper) {
    position: relative;
    float: left;
    width: 437px;
    height: 100%;
    overflow: hidden;

    &:hover {
      @include e(mask) {
        visibility: visible;
        opacity: 1;
      }
    }
  }

  @include e(mask) {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    text-align: center;
    visibility: hidden;
    background: radial-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.2));
    opacity: 0;
    transition: all 0.2s ease;
  }

  @include e(button) {
    margin-top: (142px - 37) / 2;

    @include button(120px, 37px);
    @include button-solid();
  }

  @include e(img) {
    width: auto;
    height: 100%;

    &.overdue {
      filter: grayscale(100%);
    }
  }

  @include e(info) {
    position: absolute;
    width: 760px - 437;
    height: 100%;
    padding: 0 20px;
    margin-left: 437px;
  }

  @include e(timer) {
    display: inline-block;
    padding: 0 12px;
    margin-top: 20px;
    font-size: 13px;
    line-height: 28px;
    color: $--color-primary;
    text-align: center;
    vertical-align: top;
    border: 1px solid $--color-primary;
    border-radius: 3px;

    &.overdue {
      color: #808080;
      background-color: #323232;
      border: 1px solid #323232;
    }
  }

  @include e(describe) {
    margin-top: 8px;
    font-size: 12px;
    line-height: 18px;
    color: #999;
  }

  @include e(remark) {
    position: absolute;
    bottom: 10px;
    left: 0;
    width: 100%;
    padding: 0 20px;
    overflow: hidden;
    font-size: 12px;
    color: #ccc;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  @include e(more) {
    height: 40px;
    font-size: 14px;
    line-height: 40px;
    color: #b0b0b0;
    text-align: center;
    cursor: pointer;
    background-color: #323232;
  }

  @include e(icon-more) {
    display: inline-block;
    width: 19px;
    height: 19px;
    margin-top: (40px - 19 ) / 2;
    margin-right: 4px;
    vertical-align: top;
    transition: transform 0.2s ease;

    &.open {
      transform: rotate(180deg);
    }
  }
}

$--icon-list: (0, 24, 24, -26), (1, 24, 22, -50), (2, 22, 27, -74), (3, 24, 24, -96),
  (4, 24, 16, -121), (5, 24, 24, -145), (6, 17, 24, -170), (7, 22, 19, -188);

@include b(menu) {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 255px;
  font-size: 14px;
  color: #ccc;
  background-color: #323232;
  box-shadow: 1px 0 10px rgba(34, 34, 34, 0.8);

  @include e(title) {
    height: 65px;
    font-size: 16px;
    line-height: 65px;
    text-align: center;
    background-color: #3c3c3c;
  }

  @include e(list) {
    &:hover {
      @include e(item) {
        &:not(:hover) {
          color: inherit;
          background-color: transparent;

          &::before {
            opacity: 0;
          }

          &::after {
            border-top: 2px solid #5e5e5e;
            border-right: 2px solid #5e5e5e;
          }

          @each $i, $width, $height, $position in $--icon-list {
            .icon-#{$i} {
              background-position: ($position + px) 0;
            }
          }
        }
      }
    }
  }

  @include e(item) {
    position: relative;
    height: 60px;
    margin-left: 8px;
    line-height: 60px;
    cursor: pointer;
    border-bottom: 1px solid #232323;
    transition: all 0.2s ease;

    &::before {
      position: absolute;
      top: 0;
      left: -8px;
      width: 4px;
      height: 100%;
      content: '\20';
      background-color: #282828;
      opacity: 0;
      transition: all 0.2s ease;
    }

    &::after {
      position: absolute;
      top: 25px;
      right: 20px;
      width: 8px;
      height: 8px;
      content: '\20';
      border-top: 2px solid #5e5e5e;
      border-right: 2px solid #5e5e5e;
      transition: all 0.2s ease;
      transform: rotate(45deg);
    }

    &.active,
    &:hover {
      color: #f8ad32;
      background-color: #282828;

      &::before {
        opacity: 1;
      }

      &::after {
        border-top: 2px solid #de9c33;
        border-right: 2px solid #de9c33;
      }

      @each $i, $width, $height, $position in $--icon-list {
        .icon-#{$i} {
          background-position: ($position + px) -28px;
        }
      }
    }
  }

  @include e(icon) {
    display: inline-block;
    vertical-align: middle;
    background: no-repeat;
    transition: all 0.2s ease;

    @include background-image(url(~@/views/promotions/img/icons.png));

    @each $i, $width, $height, $position in $--icon-list {
      &.icon-#{$i} {
        width: $width + px;
        height: $height + px;
        margin: 0 (58px - $width) / 2;
        background-position: $position + px 0;
      }
    }
  }
}
